body {
    font-family: $font_body;
	color: $cl_black;
    -webkit-font-smoothing: antialiased;
    -webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
    text-shadow: rgba(0,0,0,.01) 0 0 1px;
    margin: 0;
	padding: 0;
	overflow-x: hidden;

}

html {
	margin: 0;
	padding: 0;
}

html, body{
	width: 100%;
	height: 100%;
}

*, *:before, *:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}


a {
	color: inherit;
	text-decoration: none;
	//display: inline-block;
	vertical-align: top;
	outline:0;
	@include base_transition('all');
	&:hover {
		color: #ccc;
		// color: $bg;
		text-decoration: none;
	}
}
a:focus,
button:focus {
	outline: 0;
	outline: -webkit-focus-ring-color auto 0;
	outline-offset: 0;
}
strong {
	font-weight: 700;
}
img {
	max-width: 100%;
	width: auto;
	height: auto;
	vertical-align: top;
	border: 0;
	-ms-interpolation-mode: bicubic;
}

input:focus::-webkit-input-placeholder,
input:focus:-moz-placeholder,
input:focus::-moz-placeholder,
input:focus:-ms-input-placeholder {
	color: transparent;
}

h1, h2, h3 {
	margin: 0;
}

.clearfix {
	*zoom: 1;
	&:after,
	&:before {
		display: table;
		content: "";
		line-height: 0;
	}
	&:after {
		clear: both;
	}
	position: relative;
}
.container {
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	padding-right: 15px;
	padding-left: 15px;
	*zoom: 1;
    @include min-responsive($mxtablet) {
        width: $mntablet;
    }
}
.col-20,
.col-30,
.col-40,
.col-50,
.col-60,
.col-70,
.col-80,
.col-100 {
	float: left;
	position: relative;
	padding-right: 15px;
	padding-left: 15px;
	margin: 0;
}
.col-20 {
	width: 25%;
}
.col-30 {
	width: 33.33333333%;
}
.col-40 {
	width: 41.66666667%;
}
.col-50 {
	width: 50%;
}
.col-60 {
	width: 58.33333333%;
}
.col-70 {
	width: 66.66666667%;
}
.col-80 {
	width: 75%;
}
.col-100 {
	width: $grid_mobile;
}
.col-offset-fluid {
	margin-right: -15px;
	margin-left: -15px;
}
.col-offset-0 {
	padding-right: 0px;
	padding-left: 0px;
}
.mt1 {
	margin-top: 10px;
}
.mt2 {
	margin-top: 20px;
}
.mt3 {
	margin-top: 30px;
}
.mt4 {
	margin-top: 40px;
}
.mt6 {
	margin-top: 60px;
}
.mt10 {
	margin-top: 100px !important;
}
.mb1 {
	margin-bottom: 10px;
}
.mb2 {
	margin-bottom: 20px;
}
.mb3 {
	margin-bottom: 30px;
}
.mb4 {
	margin-bottom: 40px;
}
.pad0{
	padding: 0;
}
.pv2{
	padding: 20px 0px;
}
.fr{
	float: right;
}
.display-table {
	display: table;
	@include display_full();
	position: relative;
	z-index: 1;
}
.display--block{
	display: block;
}
.display-center {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	@include display_full();
}
.text-left {
	text-align: left!important;
}
.text-right {
	text-align: right;
}
.text-center {
	text-align: center;
}
ol, ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.left {
	float:left;
}
.right {
	float: right;
}

.table{
	display: table;
	margin: 0 auto;
}


.pink::selection {
  background:$cl_navy;
  color: $cl_white;
}
.pink::-moz-selection {
  background:$cl_navy;
  color: $cl_white;
}