.gn{
    &__bg{
        background: linear-gradient(149deg, rgba(23,126,148,1) 0%, rgba(250,250,250,0.1825105042016807) 44%), url('../images/body-bg.png') no-repeat;
        background-size: cover;
        background-attachment: fixed;
    }
    &__body{
        display: block;
        width: 100%;
    }
    &__wrapper{
    }
    &__title{
        margin-top: 50px;
        color: #fff;
        font-family: $font_title;
        @include min-responsive($mxtablet) {
            margin: 150px 0 40px;
        }
        &--big{
            font-weight: 200;
            line-height: 62px;
            font-size: 36px;
            text-transform: uppercase;
            @include min-responsive($mxtablet){ 
                font-size: 64px;
            }
        }
        &--lite{
            font-weight: 200;
            line-height: 62px;
            font-size: 28px;
            text-transform: uppercase;
            @include min-responsive($mxtablet) {
                font-size: 36px;
            }
        }
    }
    
    &__content{
        margin-bottom: 3rem;
        @include min-responsive($mxtablet) {
            margin-bottom: 8rem;
        }
        &--big{
            padding: 25px;
            background: rgba(0,0,0,.18);
            border-radius: 10px;
            color: #fff;
            font-family: $font_body;
            margin-bottom: 20px;
        }
    }
    &__about{
        font-family: $font_body;
        p{
            font-size: 1em;
            line-height: 1.5;
        }
        ol{
            list-style: decimal;
            padding-left: 1em;
            li{
                line-height: 1.5;
            }
        }
    }
    &__logo{
        display: flex;
        align-items: center;
        background: rgba(255,255,255,.75);
        padding: 5px 10px;
        border-radius: 10px;
        height: 100px;
        margin: 0 0px 25px;
        text-align: center;
        @include min-responsive($mxtablet){
            margin: 0 25px 25px;
        }
        img{
            max-width: 100%;
            max-height: 100%;
            margin: 0 auto;
        }
    }
}