.footermenu {
    background: $cl_navy; // background: linear-gradient(to right, #444444 0%, #000000 100%);
    // background: -webkit-linear-gradient(left, #444444 0%, #000000 100%);
    // background: -moz-linear-gradient(left, #444444 0%, #000000 100%);

    p{
        font-family: $font_footer;
        font-size: .8em;
        @include min-responsive($mxtablet){
            font-size: 1em;
        }
    }
    &__list {
        // background:$cl_navy;
        height: 50px;
        margin: 0;

    }
    &__item {
        display: inline-block;
    }
    &__link {
        font-family: $font_footer;
        color: white;
        font-weight: 700;
        font-size: 14px;
        padding-right: 20px;

        &:hover,
        &:focus {
            text-shadow: 0 1px 1px rgba(0, 0, 0, 0.58);
        }
    }

    &__gate {
        padding: 30px 0 50px;
        position: relative; // margin-bottom: 20px;
        color: white;
    }

    &__search {
        position: absolute;
        right: 15px;
        top: 0;
        height: 50px;
        background: $cl_navy;
        width: 190px;
        color: $cl_white; // @include skew(-20deg);
        &--icon {
            position: absolute;
            top: 17px;
            font-size: 17px;
            left: 8px;
        }

        &:before {
            content: "";
            width: 0;
            height: 0;
            border-top: 60px solid transparent;
            border-bottom: 0px solid transparent;
            border-right: 20px solid $cl_navy;
            position: absolute;
            left: -20px;
        }
    }

    &__input {
        width: 90%;
        height: 100%;
        position: absolute;
        right: 0;
        font-size: 16px;
        padding: 0 20px;
        border: 0;
        outline: none;
        color: cl_black;
        background: none;

        &::-webkit-input-placeholder {
            /* Chrome/Opera/Safari */
            color: cl_black;
        }
        &::-moz-placeholder {
            /* Firefox 19+ */
            color: cl_black;
        }
        &:-ms-input-placeholder {
            /* IE 10+ */
            color: cl_black;
        }
        &:-moz-placeholder {
            /* Firefox 18- */
            color: cl_black;
        }
    }

    &__social {
        position: absolute;
        top: 75px;
        right: 0;
        color: white;

        &-item {
            display: inline-block;
        }

        &-link {
            padding: 0px 5px;
        }
    }
}

.footer {
    &-content {
        display: inline-block;
        a {
            position: relative;
            padding-right: 10px;
            &:hover {
                color: $cl_white;
            }
            &::after{
                content: "";
                width: 3px;
                height: 3px;
                position: absolute;
                border-radius: 50%;
                right: 2px;
                top: 45%;
                background: white;
            }
            &:last-child{
                &::after{
                    content: "";
                    width: 3px;
                    height: 3px;
                    position: absolute;
                    border-radius: 50%;
                    right: 2px;
                    top: 45%;
                    background: transparent;
                }
            }
        }
    }
    &-logo {
        text-align: right;
    }
    &-mid {
        width: 100%;
        height: auto;
        position: relative;
        display: block;
        margin: 0 auto;
        line-height: 24px;
        padding: 0 30px;
        text-align: center;
        font-size: 12   px;
        @include min-responsive(1024px) {
            font-size: 12px;
            width: 670px;
            padding: 0 60px;
        }
        b{
            color: $cl_white;
            font-weight: 300;
            font-size: 24px;
        }
    }
    &-left {
        float: left;
        width: 230px;
    }
    &-right {
        float: right;
        width: 162px;
    }
    &__lead{
        margin-bottom: 50px;
    }
}